@media only screen and (max-width:800px)
{
  .App{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr ;
     }
     .card
  {
  
   
    background-color:white;
  
    border-radius: 1rem;
    width:50%;
    margin-left:23%;
  
    padding-top:20px;
    padding-bottom: 20px;
    margin-bottom:20px;
   margin-top:50px;
  }
}
.ul > li::marker{
  content:"✔ ";
}
.card:nth-child(1) li:nth-child(n+5)::marker
{
  content:"✖ ";
 
}
.card:nth-child(1) li:nth-child(n+5)
{
opacity:50%;
 
}
.card:nth-child(2) li:nth-child(n+8)::marker
{
  content:"✖ ";
 
}
.card:nth-child(2) li:nth-child(n+8)
{
opacity:50%;
 
}
.card:nth-child(3) li:nth-child(1)
{
  font-weight: bolder;
}
 
.card:nth-child(2) li:nth-child(1)
{
  font-weight: bolder;
}